* {
	padding: 0;
	margin: 0; }
html, body {
	font-family: 'Ubuntu', sans-serif;
	overflow-x: hidden;
	height: 100%;
	position: relative; }
.container {
	box-sizing: border-box;
	width: 100% !important;
	height: 100%;
	max-width: 1140px;
	position: relative;
	@include r(1024) {
		padding: 0 15px; } }
.row {
	height: 100%; }
.flex {
	display: flex; }
a {
	text-decoration: none;
	transition: all 0.3s; }
.bg--examples {
	background: url('../img/examples--bg.png') no-repeat {
  background-size: cover;
  background-position: 0 20%; } }
.heading {
	position: relative;
	h2 {
		font-size: 45px;
		font-weight: 500;
		color: $charcoal-grey;
		text-transform: uppercase;
		padding: 0 35px;
		margin-bottom: 35px;
		position: relative;
		background: #fcfcfc;
		z-index: 3;
		display: inline-block; }
	&__line {
		width: 100%;
		height: 2px;
		background: $blue-green;
		position: absolute;
		left: 0;
		top: 25px;
		z-index: 1; }
	p {
		font-size: 20px;
		font-weight: 500;
		color: $greyish;
		padding-bottom: 64px; } }
input::-webkit-inner-spin-button, input::-webkit-outer-spin-button {
	-webkit-appearance: none;
	margin: 0; }
input::-webkit-input-placeholder {
	color: $greyish; }
input::-moz-placeholder {
	color: $greyish; }
input:-moz-placeholder {
	color: $greyish; }
input:-ms-input-placeholder {
	color: $greyish; }
textarea::-webkit-input-placeholder {
	color: $greyish; }
textarea::-moz-placeholder {
	color: $greyish; }
textarea:-moz-placeholder {
	color: $greyish; }
textarea:-ms-input-placeholder {
	color: $greyish; }
